<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}" style="padding-top: 1rem;">
      <!--      导航-->
      <!-- <nav-head :title="title" ></nav-head> -->
      <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
        <nav-head :title="title" ></nav-head>
      </div>
      <!--      header 头部-->
      <div class="header">
        <div class="dividing-line"></div>
      </div>
      <!--主要区域  "向他购买"-->
      <div class="main">
        <!-- 标题 -->
        <div class="title clear-fix">
          <div class="left fl">新币抢购规则</div>
          <div class="right fr"  @click="toList()">我的参与 <img class="img" src="../../assets/image/youjiantou.png" alt=""> </div>
        </div>
        <!-- <h1 :style="{color:'red','font-weight':200}">这是一个H1</h1> -->
        <!--        列表区域 循环此区域-->
        <div v-for="(item,index) in coin" :key="index" class="list-item" :class="index%2!=0?'deep':''">
          <!--          标题栏-->
          <div class="list-nav-box">
            <div class="list-nav-left clear-fix">
              <span class="left-icon fl"><img :src="url+ item.pic" alt="" style="width: 100%;height: 100%;border-radius: 100%;"></span>
              <span class="fl clear-fix" style="margin-left: 0.27rem;padding-top: 0.19rem;display: block;">
                <span class="coin fl" style="margin-right: 0.1rem;overflow: hidden;text-overflow:ellipsis;white-space: nowrap;">{{item.currency_id}} </span>
                <span class="fl" style="color: #fff;opacity: 0.3;">( </span>
                <span class="fl" style="color: #fff;opacity: 0.3;max-width: 1.2rem;inline-block;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;">{{item.title}}</span>
                <span class="fl" style="color: #fff;opacity: 0.3;"> )</span>
              </span>
            </div>
             <div class="process-bar" style="flex: 1;">
                <div class="process-bar-hover" :style="{ width: ( Number(item.deal) / Number(item.total_num) *100)+'%'}"></div>
            </div>
            <span class="right-btn" @click="toCoin(item.activity_id)">{{item.start_time > setTime ? '未开始' : setTime > item.start_time && item.end_time > setTime ? '进行中' : '已结束' }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Toast } from 'vant'
import { newcoin } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import NavHead from '@/components/head.vue'
Vue.use(Toast)
export default {
  components: { NavHead },
  data () {
    return {
      url: this.GLOBAL.domainName,
      parameter: {
        activity: 'newcoin'
      },
      coin: [],
      curHeight: '',
      setTime: '',
      title: '新币预售'
    }
  },
  created () {
    var timestamp3 = Date.parse(new Date())
    console.log(timestamp3, 'timestamp3')
    this.setTime = timestamp3 / 1000
    var data = objKeySort(this.parameter)
    var token = sgin(data)
    this.parameter.token = token
    this.coinList(this.parameter)
    this.beforeMount()
  },
  methods: {
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
      // this.curHeight = h - height // 减去页面上固定高度height
    },
    coinList (parameter) {
      var that = this
      newcoin(parameter).then(res => {
        console.log(res, that)
        this.coin = res.data
      }).catch((err) => console.log(err))
    },
    toCoin (id) {
      console.log()
      this.$router.push({
        path: '/presale/details',
        query: {
          id: id
        }
      })
    },
    toList () {
      this.$router.push({
        path: '/presale/list',
        query: {
          // id: id
        }
      })
    }
  }
}
</script>
